.filter-list {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f8f9fa;
    border: 1px solid #ced4da;
    border-radius: 5px;
  }
  
  .filter-list h1 {
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
  }
  
  .dropdown-button {
    margin-bottom: 10px;
  }
  
  .search-input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 5px;
    font-size: 16px;
    outline: none;
  }
  
  .list {
    margin-top: 20px;
  }
  
  .list-item {
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ced4da;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  